// /common/http.interceptor.js

// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://api.shop.eduwork.cn',
		method: 'POST',
			// jsonJSON.parse()
			dataType: 'json',
			showLoading: true, // loading
			loadingText: '...', // loading
			loadingTime: 800, // ms
			originalData: true, // 
			loadingMask: true, // loading
		// ......
	});
	
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		config.header.Authorization = 'Bearer'+uni.getStorageSync('token');
		
		// urlurlthis.$u.get(url)url
		if(config.url == '/api/auth/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		const {statusCode,data}=res
		if(statusCode < 400){
			// vm.$u.toast("")
			return data
		}
		else if(statusCode===401) {
			//  token 
			if(data.message==="Unauthorized"){
				vm.$u.toast("")
				return false
			}else{
			vm.$u.toast(",")
			setTimeout(() => {
				// uView
					vm.$u.route('/pages/auth/login/login')
				}, 1500)
			return false}
		}else if(statusCode===400) {
			vm.$u.toast(data.message)
			return false
		}else 
		//  
		if(statusCode===422){
			// console.log('422',res)
			const {errors}=data
			const err=Object.values(errors)[0][0]
			vm.$u.toast(err)
			return false
		}
}
	// patch
	vm.$u.patch=(url,params={})=>{
		const _params={
			...params,
			_method:'PATCH'
		}
		return vm.$u.post(url,_params)
	}
}

export default {
	install
}
